table {
  margin-top: 10px; }

thead tr {
  background-color: white !important; }

tr:nth-child(odd) {
  background-color: #f9fbff; }

tr:nth-child(even) {
  background-color: white !important; }

td,
th {
  position: relative;
  border: 1px solid #d9ebf7;
  vertical-align: middle;
  padding: 0;
  text-align: left;
  height: 38px; }

td > div,
th > div {
  min-height: 20px;
  padding: 8px; }

.opt-btn > div > div:first-child {
  border-top-color: #75B3E0;
  left: 10px; }

.opt-btn > div > div:last-child {
  border: none;
  background-color: transparent;
  height: 0px;
  min-height: 0px;
  padding: 0; }

.last-add {
  padding: 0;
  border: 0;
  background-color: white; }

.last-add span {
  display: inline-block;
  position: relative;
  font-size: 20px;
  color: white;
  background-color: #6fb3e0;
  margin-left: 5px;
  width: 30px;
  height: 30px;
  text-align: center;
  border-radius: 4px; }

.last-add span:hover {
  cursor: pointer;
  background-color: #85cbf7; }

.operation-btn {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  position: absolute;
  right: -50px;
  top: -30px;
  font-size: 22px;
  z-index: 10; }

.operation-btn span,
.operation-btn .last-add {
  display: inline-block;
  position: relative;
  font-size: 20px;
  color: white;
  background-color: #6fb3e0;
  width: 25px;
  text-align: center;
  border-radius: 4px; }

.operation-btn span:last-child,
.operation-btn .last-add:last-child {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0; }

.operation-btn span:first-child,
.operation-btn .last-add:first-child {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0; }

.operation-btn span:hover,
.operation-btn .last-add:hover {
  background-color: #85cbf7; }

.column-title {
  cursor: text; }

.column-title:hover span {
  cursor: pointer;
  visibility: visible; }

.row-content td:last-child {
  cursor: pointer;
  visibility: hidden;
  border: none;
  background-color: white; }

.row-content:hover td:last-child {
  visibility: visible; }

tbody .operation-btn {
  left: 0;
  top: -5px; }

.hide {
  visibility: hidden !important; }

td {
  min-width: 60px;
  position: relative; }

td input {
  position: absolute;
  outline: none;
  left: 5px;
  top: 5px;
  padding: 2px 2px;
  min-width: 40px;
  min-height: 22px;
  color: rgba(0, 0, 0, 0.65);
  border: 1px solid #d9d9d9; }

td input:hover {
  border-color: #40a9ff; }

td input:focus {
  border-color: #40a9ff;
  outline: 0; }



