@charset "UTF-8";
body {
  overflow: auto !important; }

table td {
  text-align: center; }

.App {
  padding-top: 50px;
  padding-left: 20px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

.App div {
  border-bottom-right-radius: 0;
  border-top-right-radius: 0; }

.App > ul + div + button {
  border-bottom-left-radius: 0;
  border-top-left-radius: 0; }

.main-container {
  margin-left: 20px; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
  animation: App-logo-spin infinite 20s linear;
  height: 80px; }

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white; }

.App-title {
  font-size: 1.5em; }

.App-intro {
  font-size: large; }

.toolbar {
  height: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
  align-items: flex-end; }

.toolbar i {
  position: relative;
  top: -2px;
  margin-left: 10px;
  color: grey;
  -webkit-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease; }

.toolbar i:hover {
  cursor: pointer;
  -webkit-transform: translateY(-4px) scale(1.05);
  -ms-transform: translateY(-4px) scale(1.05);
  transform: translateY(-4px) scale(1.05);
  opacity: .7; }

.helpModal {
  width: 800px !important; }

.helpModal code {
  font-size: 19px;
  color: #1890ff; }

.scrollbar ul .scrollbar::-webkit-scrollbar,
.scrollbar ul::-webkit-scrollbar {
  width: 5px;
  height: 7px; }

/*定义滚动条的轨道，内阴影及圆角*/
.scrollbar::-webkit-scrollbar-track,
.scrollbar ul::-webkit-scrollbar-track {
  background-color: transparent; }

/*定义滑块，内阴影及圆角*/
.scrollbar::-webkit-scrollbar-thumb,
.scrollbar ul::-webkit-scrollbar-thumb {
  height: 10px;
  border-radius: 5px;
  background-color: rgba(69, 139, 200, 0.5); }

.menu {
  position: fixed;
  background-color: white;
  width: 100%;
  z-index: 998;
  top: 0;
  -webkit-box-shadow: 0 1px 8px #f0f1f2;
  box-shadow: 0 1px 8px #f0f1f2; }

.anchor-container {
  background-color: black; }

.anchor-container > div > div > div {
  position: fixed;
  left: 85vw;
  top: 20vh; }



