@charset "UTF-8";
.tab_container {
  max-width: 80vw; }

.tab-title {
  position: relative;
  height: 50px;
  color: grey;
  font-size: 20px;
  max-width: 80vw;
  padding: 10px 5px;
  -webkit-transition: background-color 0.2s ease;
  -o-transition: background-color 0.2s ease;
  transition: background-color 0.2s ease; }

.tab-title:hover {
  background-color: rgba(240, 240, 240, 0.9); }

.tab-title:after {
  content: "";
  position: absolute;
  left: 5px;
  bottom: 0;
  width: 80vw;
  height: 1px;
  background-color: #6FB3E3; }

.tab-title span:nth-child(2) {
  margin-left: 10px;
  font-size: 15px;
  color: #b4b4b4; }

.tab-title > i {
  position: absolute;
  right: 5px;
  font-size: 24px;
  color: #08c;
  -webkit-transition: -webkit-transform 0.3s ease;
  transition: -webkit-transform 0.3s ease;
  -o-transition: transform .3s ease;
  transition: transform .3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease; }

.tab-title > i:hover {
  cursor: pointer;
  -webkit-transform: translateY(-2px) scale(1.1, 1.1);
  -ms-transform: translateY(-2px) scale(1.1, 1.1);
  transform: translateY(-2px) scale(1.1, 1.1); }

.modal-mask {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: #373737;
  background-color: rgba(0, 0, 0, 0.65);
  height: 100%;
  z-index: 1000; }

.modal-wrap {
  position: fixed;
  overflow: auto;
  margin: 0;
  padding: 0;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  -webkit-overflow-scrolling: touch;
  outline: 0; }

.modal-wrap .modal-content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  font-size: 14px;
  line-height: 1.5;
  color: rgba(0, 0, 0, 0.65);
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  text-align: center;
  margin: 0 auto;
  margin-top: 5vh;
  padding: 0;
  list-style: none;
  position: relative;
  width: 90vw;
  height: 90vh;
  overflow: hidden;
  z-index: 1001; }

.modal-wrap .modal-content img {
  z-index: 1001; }

.modal-wrap .modal-content .modal-plus {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: inline-block;
  position: fixed;
  width: 100px;
  height: 40px;
  color: white;
  font-size: 22px;
  z-index: 1010;
  top: 50vh;
  left: 0;
  right: 0;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.7); }

.modal-wrap .modal-content i {
  cursor: pointer;
  -webkit-transition: -webkit-transform 0.5s ease;
  transition: -webkit-transform 0.5s ease;
  -o-transition: transform .5s ease;
  transition: transform .5s ease;
  transition: transform 0.5s ease, -webkit-transform 0.5s ease;
  opacity: 1 !important; }

.modal-wrap .modal-content i:hover {
  -webkit-transform: rotateZ(90deg);
  -ms-transform: rotate(90deg);
  transform: rotateZ(90deg); }

.modal-wrap .modal-content .modal-scale {
  position: fixed;
  bottom: 10vh;
  left: 0;
  right: 0;
  max-width: 500px;
  margin: auto;
  z-index: 1010;
  padding: 50px;
  -webkit-transition: opacity .3s ease;
  -o-transition: opacity .3s ease;
  transition: opacity .3s ease; }

.modal-wrap .modal-content .modal-scale:hover {
  opacity: 1 !important; }

.modal-wrap .modal-content .modal-scale button {
  color: white;
  line-height: 0;
  font-size: 50px;
  background-color: rgba(0, 0, 0, 0.7);
  width: 80px;
  height: 50px;
  outline: 0;
  border: none;
  -webkit-transition: background-color 0.3s ease;
  -o-transition: background-color 0.3s ease;
  transition: background-color 0.3s ease; }

.modal-wrap .modal-content .modal-scale button:first-child {
  margin-right: 5px; }

.modal-wrap .modal-content .modal-scale button:hover {
  cursor: pointer;
  background-color: rgba(50, 50, 50, 0.7); }

.table-container button {
  position: relative;
  top: 10px;
  left: 10px; }

.scrollbar::-webkit-scrollbar,
.scrollbar ul::-webkit-scrollbar {
  width: 5px;
  height: 7px; }

/*定义滚动条的轨道，内阴影及圆角*/
.scrollbar::-webkit-scrollbar-track,
.scrollbar ul::-webkit-scrollbar-track {
  background-color: #222844; }

/*定义滑块，内阴影及圆角*/
.scrollbar::-webkit-scrollbar-thumb,
.scrollbar ul::-webkit-scrollbar-thumb {
  height: 10px;
  border-radius: 5px;
  background-color: rgba(250, 250, 250, 0.9); }



